
.preview_ipr{
    position: relative;
}

.preview_ipr .wrap{
    position: relative;
    z-index: 2
}

.preview_ipr::after{
    content: "";
    position: absolute;
    display: block;
    right: 15%;
    height: 450px;
    width: 45%;
    opacity: 1;
    top: 0;
    background: radial-gradient(50% 50% at 50% 50%, #c4c4c400 0%, #181818 100%);
}
.preview_ipr::before{
    content: "";
    position: absolute;
    display: block;
    right: 15%;
    height: 450px;
    width: 45%;
    top: 0;
    opacity: 0.15;
    background: url(/img/main/mask_38.png)no-repeat center;
    background-size: cover;
}


.preview_ipr{
    

    .title_params_preview_irp{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: center;
        margin-bottom: 45px;
    }

    .title_preview_irp{
        font-weight: 400;
        font-size: 36px;
        color: white;
        line-height: 1.15;
    }

    .params_preview_irp{
        display: flex;
    }

    .param_item_preview_irp{
        display: flex;
        align-items: center;
        padding: 0 20px;
        background: white;
        border-radius: 70px;
        font-weight: 500;
        height: 30px;
        font-size: 15px;
        line-height: 18px;
        margin-left: 5px;
        color: #181818;
    }
    .info_box_irp{
        width: 400px;
    }
    .swiper_info_box_irp{
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid rgb(255,255,255, 0.2);
        padding-bottom: 48px;
        margin-bottom: 60px;
    }
    .swiper_box_irp{
        width: 750px;
    }
    .swiper_irp{
        transform: rotateY(180deg);
        position: relative
    }
    .content_swiper_slide_irp{
        transform: rotateY(180deg);
        width: 100%;
        height: 535px;
        background: white;
    }
    .swiper{
        overflow: inherit!important;
    }
    .swiper-slide{
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        color: black;
        display: flex;
        justify-content: flex-end;
    }

    .swiper-slide img{
        transform: rotateY(180deg);
        object-fit: cover;
        position: relative;
        width: calc(100% - 35px);
        
    }

    .text_in_photo_swiper_irp{
        transform: rotateY(180deg);
        position: absolute;
        right: 37px;
        color: white;
        bottom: 21px;
        font-weight: 400;
        font-size: 16px;
        line-height: 100%;
    }
    .btn_play_video_swiper_irp{
        position: absolute;
        bottom: 0;
        left: 35px;
        background: #3C4AD2;
        width: 316px;
        height: 58px;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        transform: rotateY(180deg);
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        z-index: 3
    }
    .swiper_control_irp{
        width: calc(100% - 35px);
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 32px;
    }

    .swiper-pagination_irp{
        display: flex;
        align-items: center;;
    }

    .swiper_control_irp .swiper-pagination_irp .swiper-pagination-current {
        color: #fff;
    }

    .swiper_control_irp .swiper-pagination_irp .line {
        display: block;
        width: 102px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        margin-right: 22px;
        margin-left: 22px;
    }
    
    .swiper-arrows_irp{
        // transform: rotateY(180deg);
        display: flex;
        align-items: center;
    }
    .swiper-button-prev_irp, .swiper-button-next_irp{
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: 0.3s all;
        position: relative
    }
    .swiper-button-prev_irp:hover, .swiper-button-next_irp:hover {
        background-color: rgba(255, 255, 255, 0.1);
        cursor: pointer
    }
    .swiper-arrows_irp__separator{
        display: block;
        height: 34px;
        margin: 0 10px;
        width: 1px;
        background: rgba(255, 255, 255, 0.2);
    }

    .company_info_irp{
        padding-bottom: 23px;
        margin-bottom: 30px;
        border-bottom: 1px solid rgb(255,255,255, 0.2);
    }

    .mini_title_company_info_irp{
        font-weight: 400;
        font-size: 16px;
        line-height: 1.1;
        margin-bottom: 8px;
    }

    .logo_name_company_info_irp{
        display: flex;
        align-items: center;
    }

    .logo_company_info_irp{
        width: 46px;
        height: 46px;
        border-radius: 50%;
        object-fit: contain;
    }

    .name_company_info_irp{
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: white;
        margin-left: 7px;
        width: calc(100% - 53px);
    }

    .box_manager_info_irp{
        display: flex;
        align-items: center;
        border-bottom: 1px solid rgb(255,255,255, 0.2);
        margin-bottom: 30px;
        padding-bottom: 30px;
    }

    .manager_photo_irp{
        width: 109px;
        height: 109px;
        object-fit: cover;
        border-radius: 50%
    }

    .manager_info_irp{
        margin-left: 25px;
        width: calc(100% - 134px)
    }

    .mini_title_manager_info_irp{
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        margin-bottom: 10px;
    }

    .fio_manager_info_irp{
        font-weight: 500;
        font-size: 20px;
        display: block;
        line-height: 24px;
        color: white;
    }

    .connection_items_manager_info_irp{
        margin-top: 10px;
    }

    .connection_item_manager_info_irp{
        margin-bottom: 9px;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
    }

    .mini_title_object_info_irp{
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
    }

    .full_price_object_info_irp{
        font-weight: 500;
        font-size: 48px;
        line-height: 57px;
        color: white;
    }

    .price_square_object_info_irp{
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: white;
    }

    .btn_object_info_irp{
        width: 317px;
        height: 70px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #3C4AD2;
        color: white;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        margin-top: 24px;
    }

    .ipoteka_object_info_irp{
        margin-top: 13px;
        text-align: center;
        width: 317px;
        display: block;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
    }    
}

.description_ipr{
    position: relative;

    .wrap{
        position: relative;
        z-index: 2;
    }

    .title_text_box_description_ipr{
        display: flex;
        justify-content: space-between;
    }

    .title_description_ipr{
        font-weight: 400;
        font-size: 36px;
        line-height: 1;
        color: white;
    }
    
    .text_description_ipr{
        width: 715px;
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
    }

    .items_description_irp{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        padding-top: 70px;
        border-bottom: 1px solid rgb(255,255,255, 0.2)
    }

    .item_description_irp{
        width: calc(33% - 15px);
        display: flex;
        align-items: center;
        margin-bottom: 60px;
    }

    .img_item_description_irp{
        width: 93px;
        height: 93px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #2C2C2C;
    }

    .img_item_description_irp img{
        object-fit: contain;
        width: 43px;
        margin: 0px auto
    }

    .info_item_description_irp{
        width: calc(100% - 120px);
        margin-left: 25px;
    }

    .title_item_description_irp{
        font-weight: 400;
        font-size: 68px;
        line-height: 99.52%;
        margin-top: 5px;
        color: white;

    }

    .title_item_description_irp sup{
        font-size: 0.5em;
        top: -0.8em
    }


}
.description_ipr::after{
    content: "";
    position: absolute;
    display: block;
    right: calc(50% - 250px);
    height: 500px;
    width: 500px;
    opacity: 1;
    top: -140px;
    background: #3C4AD2;
    opacity: 0.2;
    filter: blur(254px);
}
.tabs_ipr{

    .wrap{
        margin-top: 24px;
        max-width: 1240px;
        background: white;
        color: #181818;
        padding: 50px 0;
        box-sizing: border-box;
        position: relative
    }

    .wrap:after{
        content: "";
        position: absolute;
        display: block;
        left: 100%;
        width: 2000px;
        bottom: 0;
        top: 0;
        background-color: #fff;
    }
    .tabs_ipr__nav{
        display: flex;
        margin-bottom: 13px;
        padding: 0 50px;

        li{
            font-weight: 400;
            font-size: 36px;
            line-height: 1;
            opacity: 0.2;
            padding-bottom: 21px;
            margin-right: 40px;
            cursor: pointer;
            transition: 0.3s all;
        }
        li:hover{
            opacity: 1;
        }
        li.active{
            opacity: 1;
            border-bottom: 1px solid #181818;
        }
    }

        
    .tabs_ipr__content{
        display: none;   
    }
    .tabs_ipr__content.active{
        display: block;
    }

    .info_item_content_tabs_ipr{
        padding: 23px 0;
        display: flex;
        width: 580px;
        margin-left: 50px;
        align-items: center;
        border-bottom: 1px solid rgb(24,24,24, 0.2);
    }

    .title_info_item_content_tabs_ipr{
        font-weight: 400;
        font-size: 15px;
        line-height: 18px;
        opacity: 0.5;
        width: 400px;
    }

    .value_info_item_content_tabs_ipr{
        font-weight: 700;
        font-size: 15px;
        line-height: 19px;
    }

    .text_item_content_tabs_ipr{
        padding: 20px 50px;
        font-weight: 400;
        font-size: 16px;
        opacity: 0.8;
        line-height: 1.6;
    }

    #map_item_content_tabs_ipr{
        width: calc(100% - 50px);
        margin-left: 50px;
        margin-top: 20px;
        height: 500px;
        position: relative;
        iframe{
            width: 100%;
            height: 100%;
        }
    }
    
    #map_item_content_tabs_ipr canvas {
        -webkit-filter: grayscale(100%)!important;
    }
    
    #map_item_content_tabs_ipr .ymaps-2-1-79-ground-pane {
        -webkit-filter: grayscale(100%)!important;
    }
}

.custom_irp{
    padding-top: 77px;
    position: relative;
    overflow: hidden;
}

.custom_irp::after{
    content: "";
    position: absolute;
    display: block;
    right: 0;
    height: 450px;
    width: 45%;
    opacity: 1;
    top: -150px;
    background: radial-gradient(50% 50% at 50% 50%, #c4c4c400 0%, #181818 100%);
}
.custom_irp::before{
    content: "";
    position: absolute;
    display: block;
    right: 0;
    height: 450px;
    width: 45%;
    top: -150px;
    opacity: 0.15;
    background: url(/img/main/mask_38.png)no-repeat center;
    background-size: cover;
}
.articles_ipr{
    margin-top: 55px;
    
    .btn{
        border: 1px solid rgb(60, 74, 210, 1);
        background: rgb(60, 74, 210, 0)
    }
    .btn:hover{
        border: 1px solid rgb(60, 74, 210, 1);
        background: rgb(60, 74, 210, 1);
        box-shadow: 0 0 0 rgb(24,24,24, 0)
    }
}

.articles_ipr:before{
    display: none
}
.articles_ipr:after{
    display: none
}

